import customShowToast from './custom_toast.js'

/**
 * APPAPI
 * @example uni.canIUse('getSystemInfoSync.return.safeArea.left')
 * @Description  ${API}.${method}.${param}.${option}  ${component}.${attribute}.${option} 
 * @param {string} ${API} API 
 * @param {string} ${method}return, success, object, callback
 * @param {string} ${param}
 * @param {string} ${option}
 * @param {string} ${component}
 * @param {string} ${attribute}
 * @param {string} ${option}
 */
export const canIUse = (param) => {
	const _CANIUSE = uni.canIUse(param)
	return _CANIUSE
}

/**
 * 
 * @returns {object} res
 * @returns {object.platform} res.platform 
 */
export const canSystemInfoSync = () => {
	try {
		const res = uni.getSystemInfoSync()
		return res
	} catch (e) {
		console.error(e)
		customShowToast('')
	}
}
